import "./libs/polyfill/window/promise.js";
import "./libs/polyfill/window/fetch.js";

/* Constructor for the MapView, a singleton that manages the state of the map. */
function MapView(map) {

	this.map = map;
    this.zoomControl = null;
	this.tileLayer = null;
	this.timeLayers = null;
	this.d60Layer = null;
	this.d30Layer = null;
    this.w30Layer = null;
    this.centres = null;

    /* Mapbox tileLayer settings {id} */
    const tileLayerPath = "https://api.mapbox.com/styles/v1/hawkinsohocl/cjxa85mpa01ws1ckezki29p3i/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoiaGF3a2luc29ob2NsIiwiYSI6IjNmMmZkMzY4MTUwZGNiMjE4NTExOWQwNDBjNzg4NjAzIn0.bv8BBlUo7MtQR544YviGZQ";
    const tileLayerAttribution = "<a href=\"http://commonslibrary.parliament.uk\">House of Commons Library</a>, Imagery Â© <a href=\"http://mapbox.com\">Mapbox</a>";
    const tileLayerAccessToken = "pk.eyJ1IjoiaGF3a2luc29ob2NsIiwiYSI6IjNmMmZkMzY4MTUwZGNiMjE4NTExOWQwNDBjNzg4NjAzIn0.bv8BBlUo7MtQR544YviGZQ";
    const tileLayerMinZoom = 5;
    const tileLayerMaxZoom = 18;

    this.tileLayer = L.tileLayer(tileLayerPath, {
    	attribution: tileLayerAttribution,
        accessToken: tileLayerAccessToken,
        minZoom: tileLayerMinZoom,
        maxZoom: tileLayerMaxZoom
    });

	// Set the map to update the query string with the current position
	this.map.on("moveend", () => {
		const c = map.getCenter();
		const z = map.getZoom();
		const url = `?long=${c.lng}&lat=${c.lat}&zoom=${z}`;
		history.replaceState(null, null, url);
	});

	// Sets up the map from the downloaded data
	this.setupMap = function(d60Layer, d30Layer, w30Layer, centreMarkers) {

        this.zoomControl = L.control.zoom({position:'topright'});

		this.d60Layer = d60Layer;
		this.d30Layer = d30Layer;
		this.w30Layer = w30Layer;
        this.centreMarkers = centreMarkers;

		var layers = {
			'Driving within 60 minutes': d60Layer,
			'Driving within 30 minutes': d30Layer,
			'Walking within 30 minutes': w30Layer
		};

		// Creates a layer group control
		this.timeLayers = L.control.layers({}, layers,
			{position: 'bottomright', collapsed: false});

		this.d60Layer.setZIndex(1);
		this.d30Layer.setZIndex(2);
		this.w30Layer.setZIndex(3);

		this.tileLayer.addTo(this.map);
		this.d60Layer.addTo(this.map);
		this.d30Layer.addTo(this.map);
		this.w30Layer.addTo(this.map);

        this.centreMarkers.forEach(marker => {
            marker.addTo(this.map);
        });

        this.timeLayers.addTo(this.map);
        this.zoomControl.addTo(this.map);
	};
};


/* Constructor for the MapModel object, a singleton that manages the MapView. */
function MapModel(mapView) {

    this.mapView = mapView;
    this.dataDownloads = 4;
    this.d60Json = null;
    this.d30Json = null;
    this.w30Json = null;
    this.centres = null;
    this.d60Layer = null;
    this.d30Layer = null;
    this.w30Layer = null;
    this.centreMarkers = null;

	// Sets up the initial map by loading the regional boundaries
	this.downloadMapData = function(regionCode) {

        /* Load data */
        d3.json("drive-60.json").then(json => {
            this.d60Json = json;
            this.d60Layer = L.geoJson(this.d60Json, {
                color: '#046a38',
                opacity: 0,
                weight: 1,
                fillOpacity: 0.4});
            this.registerDownload();
        });

        d3.json("drive-30.json").then(json => {
            this.d30Json = json;
            this.d30Layer = L.geoJson(this.d30Json, {
                color: '#046a38',
                opacity: 0,
                weight: 1,
                fillOpacity: 0.4});
            this.registerDownload();
        });

        d3.json("walk-30.json").then(json => {
            this.w30Json = json;
            this.w30Layer = L.geoJson(this.w30Json, {
                color: '#046a38',
                opacity: 0,
                weight: 1,
                fillOpacity: 0.4});
            this.registerDownload();
        });

        d3.csv("scanning-centres.csv").then(data => {
            this.centres = data;
            this.centreMarkers = [];
            this.centres.forEach(location => {
                let marker = L.marker([+location.longitude, +location.latitude]);
                marker.bindPopup(`<b>${location.name}</b><br>${location.postcode}`, {closeButton: false}).openPopup();
                this.centreMarkers.push(marker);
            });
            this.registerDownload();
        });
	}

    // Tracks asynhcronous downloads
	this.registerDownload = function() {
		this.dataDownloads = this.dataDownloads - 1;
		if (this.dataDownloads === 0) {
			this.mapView.setupMap(
                this.d60Layer,
                this.d30Layer,
                this.w30Layer,
                this.centreMarkers
            );
		}
	};
}

function start(long, lat, zoom) {

    // Set up application object
    const sc = {};

	// Check the parameters are sane and set to defaults if not
    const getFloatParam = (val, defVal) => {
        if (val == null || val == false || isNaN(parseFloat(val))) {
            return defVal;
        } else {
            return parseFloat(val);
        }
    };

    const longitude = getFloatParam(long, -4.0);
    const latitude = getFloatParam(lat, 54.8);
    const zoomLevel = getFloatParam(zoom, 6);

    // Create the map
    const map = L.map('map', {
		zoomControl: false,
		maxBounds: L.latLngBounds(
			L.latLng(73.0, -60.0),
			L.latLng(25.0, 50.0))
		});
    map.setView([latitude,  longitude], zoomLevel);

    // Initialise the MVC objects
    const mapView = new MapView(map);
    const mapModel = new MapModel(mapView);

    // Add an interface to the mvc objects
    sc.mapView = mapView;
    sc.mapModel = mapModel;

    // Setup the map
    mapModel.downloadMapData();

    return sc;
}

function getQueryString(param) {
    const qs = window.location.search.substring(1);
    const v = qs.split("&");
    for (let i = 0; i < v.length; i++) {
        const p = v[i].split("=");
        if (p["0"] == param) return p["1"];
    }
    return null;
};


const long = getQueryString("long");
const lat = getQueryString("lat");
const zoom = getQueryString("zoom");
window.sc = start(long, lat, zoom);
